import * as React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { FluentProvider } from '@fluentui/react-components';
import { planktonTheme } from '../planktonTheme';
import HelpSection from "../components/help-section";
import {useTranslation} from 'gatsby-plugin-react-i18next';
import { graphql } from 'gatsby';

const SevicesPage: React.FC = () => {
  const { t } = useTranslation();
  return(
    <Layout>
        <FluentProvider theme={planktonTheme} >
        <div style={{padding:'80px 0px'}}></div>
        <div className="flex-row">
            <div className="flex-12 flex-column">
                <p className="flex-row" style={{fontSize: '70px', padding:'50px 0px', width:'inherit', lineHeight: "1.2"}}>{t('Our Services')}</p>
                <p className="flex-row" style={{paddingBottom: '50px'}}>{t('We will make you easier to directly contact the right person based on your idea and your stage! Checkout the bellow cases and choose one to continue making your idea into digital reality')}</p>
           
                </div>
            </div>
        <HelpSection displayTitle="none" backgroundColor="#ffffff" isIndex={false}/>

        </FluentProvider>
    </Layout>
  );
}
export const Head = () => <Seo title="Sevices" />

export default SevicesPage
export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;